<template>
  <div class='container'>
    <div class="row">
      <div class="col-8">
        <h1>{{roomData.title}}</h1>
        <div v-for="(item,key) in roomData.content" :key='key'>
          <h2>{{item[0]}}</h2>
          <p>{{item[1]}}</p>  
        </div>
      </div>
      <div class="col-4 border flex-center">
        <img class="border rounded-circle" :src="author_img" alt="" height="100px" width="100px">
        <h2>{{authorData.title}}</h2>
      </div>
    </div>
  </div>
</template>
<script>
import Card from '@/components/card.vue'
export default {
  components: {
    Card
  },
  data() {
    return {
      roomData:[],
      authorData:{},
      author_img: ''
    }
  },
  computed:{
  },
  created() {
    import(`@/roomData/${this.$route.params.roomId}/main.js`)
    .then((data)=>{
      this.roomData = data.default
      this.authorData = this.$store.state.allAuthors[this.roomData.author]
      this.author_img = require(`@/authorData/${this.authorData.id}.jpg`)
    })
  }
}
</script>
