<template>
  <div class>
    <section class="my-3 w-100 flex-center container">
      <img src="@/assets/image/LOGO2.svg" height="60px" alt>
      <h2 class="font-weight-bold">全台密室搜尋器</h2>
    </section>
    <section class="mb-3">
      <div class="container">
        <div class="row">
          <div class="col-6">
            <button class="btn btn-primary mb-3 w-100" @click="filterByClass(true)">顯示全部</button>
          </div>
          <div class="col-6">
            <div class="input-group">
              <input
                aria-describedby="basic-addon2"
                aria-label="Recipient's username"
                class="form-control"
                placeholder="依標題搜尋"
                type="text"
                v-model="searchString"
              >
              <div class="input-group-append">
                <button class="btn btn-outline-primary" type="button" @click="filterByName()">搜尋</button>
              </div>
            </div>
          </div>
          <!--filter-->
          <div class="col-12 filter-section">
            <div class="row bg-primary mx-0 rounded pt-3">
              <div class="col-12">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">類型</span>
                  </div>
                  <div class="form-control row mx-0 d-flex tag-group">
                    <div class="col d-flex align-items-center">
                      <input
                        id="ghost"
                        type="checkbox"
                        value=".tag1"
                        v-model="filterConditions.tag"
                      >
                      <label class="d-flex" for="ghost">
                        <img src="@/tagImg/ghost.svg" height="24px" alt>
                        <h6>恐怖</h6>
                      </label>
                    </div>
                    <div class="col d-flex align-items-center">
                      <input
                        id="couple"
                        type="checkbox"
                        value=".tag2"
                        v-model="filterConditions.tag"
                      >
                      <label for="couple" class="d-flex">
                        <img src="@/tagImg/couple.svg" height="24px" alt>
                        <h6>感人</h6>
                      </label>
                    </div>
                    <div class="col d-flex align-items-center">
                      <input
                        id="battle"
                        type="checkbox"
                        value=".tag3"
                        v-model="filterConditions.tag"
                      >
                      <label for="battle" class="d-flex">
                        <img src="@/tagImg/battle.svg" height="24px" alt>
                        <h6>團戰</h6>
                      </label>
                    </div>
                    <div class="col d-flex align-items-center">
                      <input id="plot" type="checkbox" value=".tag4" v-model="filterConditions.tag">
                      <label for="plot" class="d-flex">
                        <img src="@/tagImg/plot.svg" height="24px" alt>
                        <h6>劇情</h6>
                      </label>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-sm-4 col-6">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">難度</span>
                  </div>
                  <select class="custom-select" v-model="filterConditions.isSimple">
                    <option selected value="">不篩選</option>
                    <option value=".simple">簡單</option>
                    <option value=".hard">困難</option>
                  </select>
                </div>
              </div>
              <div class="col-sm-4 col-6">
                <div class="input-group mb-3">
                  <select class="custom-select" v-model="filterConditions.peopleMake">
                    <option selected value="0">不篩選</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                    <option value="6">6</option>
                  </select>
                  <div class="input-group-append">
                    <label class="input-group-text" for="inputGroupSelect02">人成團</label>
                  </div>
                </div>
              </div>
              <div class="col-sm-4 col-12">
                <button class="btn btn-light mb-3 w-100" @click="filterByClass()">依條件搜尋</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section class="container">
      <div id="card-group" class="row" ref="grid">
        <div class="grid-sizer col-sm-6 col-12 col-lg-4"></div>
        <div
          class="mb-4 grid-item col-sm-6 col-12 col-lg-4"
          v-for="(item,index) in rooms"
          :key="index"
          v-bind:class="setCardsClass(item.peopleMake,item.isSimple,item.tag)" 
        >
          <Card :room="item"/>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import Card from "@/components/card.vue";
import Carousel from "@/components/carousel.vue";
export default {
  name: "index",
  components: {
    Card,
    Carousel
  },
  data() {
    return {
      blocks: "123132",
      rooms: [],
      msnry: null,
      searchString: "",
      filterConditions: {
        tag: [],
        isSimple: ".simple",
        peopleMake: '0'
      }
    };
  },
  methods: {
    filterByName() {
      const vm = this;
      this.msnry.arrange({
        filter(el) {
          let title = el.querySelector(".card-title").textContent;
          if (title.search(vm.searchString) !== -1) {
            return true;
          }
        }
      });
    },
    filterByClass(isAll = false) {
      let classList = "*";
      if (!isAll) {
        classList = `${this.filterConditions.isSimple}${this.filterConditions.tag.join("")}`
        console.log(this.filterConditions.peopleMake)
        if(this.filterConditions.peopleMake !== '0'){
          classList += `.people${this.filterConditions.peopleMake}`
        }
      }
      console.log('classList:',classList);
      this.msnry.arrange({ filter: classList })
    },
    setCardsClass(peopleMake, isSimple, tagList) {
      let tempList = [];
      tempList.push(
        `people${peopleMake}`,
        isSimple ? "simple" : "hard",
        tagList.map(el => `tag${el}`)
      );
      return tempList;
    },
    initMsnry() {
      let Isotope = require("isotope-layout");
      this.msnry = new Isotope(this.$refs.grid, {
        itemSelector: ".grid-item",
        percentPosition: true,
        masonry: {
          columnWidth: ".grid-sizer"
        }
      });
    }
  },
  created() {
    this.rooms = this.$store.state.allRooms;
  },
  mounted() {
    this.initMsnry();
  },
  beforeUpdate() {
  },
  updated() {
  }
};
</script>
<style lang="scss" scoped>
.logo-svg {
  height: 100px;
}
.carousel {
  height: 800px;
}
.grid-sizer {
  display: none;
}
label {
  margin-bottom: 0;
}
.tag-group {
  height: auto;
}
.condition-cover{
  height: 40px;
  top: 0;
  z-index: -2;
  background-color: inherit;
  transform: translateY(50%);
}
*{
  box-sizing: border-box
}
</style>

