<template>
  <div class="warp position-relative rounded">
    <a class="position-absolute w-100 h-100" :href="room.url"></a>
    <div class="h-65 w-100 card-img position-relative" v-bgimg='`${room.id}/card.jpg`'>
      <div class="position-absolute m-3">
        <h4 class="simple-text text-white font-weight-bold" v-if="room.isSimple">簡單</h4>
        <h4 class="simple-text font-weight-bold text-white" v-if="!room.isSimple">困難</h4>  
      </div>
      <div class="position-absolute people-group m-3">
        <img class="people" src="@/tagImg/man-user.svg" height="22px" alt="" v-for="(item,index) in room.peopleMake" :key="index">
      </div>
    </div>
    <div class="d-flex h-35 flex-column px-3">
      <h5 class="font-weight-bold text-center my-auto card-title">{{room.title}}</h5>
      <div class="d-flex mb-3">
        <div class="tag-item flex-center ml-3" v-for="(item,index) in room.tag" :key="index">
          <img :src="tagUrl($store.state.tagData[item]['url'])" alt="">
        </div>
        <span class="text-primary ml-auto font-weight-bold mr-3">NT{{room.price}}起</span> 
      </div>  
    </div>
  </div>
</template>
<script>
export default {
  name: 'singleCard',
  props:['room'],
  directives: {
    bgimg: {
      bind: function (el, binding){
        const url = require(`@/roomData/${binding.value}`)
        el.style.backgroundImage = `url(${url})`
        el.style.backgroundSize = 'cover'
      }
    }
  },
  computed: {
    imgUrl() {
      return require(`@/roomData/${this.room.id}/card.jpg`)
    },
  },
  methods:{
    tagUrl(fileName) {
      const url = require(`@/tagImg/${fileName}`)
      return url
    }
  },
  created() {
  }
}
</script>

<style lang="scss" scoped>
.warp{
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.329);
  background-size: cover;
  height:300px;
  background-color: #fff;
}
div{
  color:black;
}
.tag-item,.tag-item img{
  height: 22px;
  width: 22px;
  opacity: 0.5;
}
.people-group{
  right: 0;
}
.simple-text{
  text-shadow: 1px 0 0 #000, -1px 0 0 #000, 0 1px 0 #000, 0 -1px 0 #000;
}
</style>
